* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  outline: none !important;
}
html {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}
h1 {
  margin: 0;
}
body {
  width: 100%;
  max-width: 100%;
  font-size: 16px;
  position: relative;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  transition: 0.3s;
  overflow-x: hidden;
  color: #000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1300px) {
  body {
    font-size: 12px;
  }
}
@media (max-width: 1024px) {
  body {
    font-size: 16px;
  }
}
@media (max-width: 680px) {
  body {
    font-size: 5vw;
  }
}
.container {
  padding: 0 30px;
  margin: 0 auto;
  box-sizing: border-box;
  width: 1300px;
}
@media (max-width: 1300px) {
  .container {
    width: 1024px;
    padding: 0 20px;
  }
}
@media (max-width: 1024px) {
  .container {
    width: 680px;
    padding: 0 20px;
  }
}
@media (max-width: 680px) {
  .container {
    width: 100%;
    padding: 0 5%;
  }
}
.text {
  font-size: .875em;
  color: #000;
  font-weight: 500;
  line-height: 1.2;
}
@media all and (max-width: 1024px) {
  .text {
    font-size: 1em;
  }
}
.btn {
  -webkit-appearance: none;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid;
  box-sizing: border-box;
  line-height: 1.58;
  height: 1.65em;
  text-align: center;
  padding: 0 1.6em;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: bold;
}
.btn--orange {
  background-color: #F18700;
  color: #fff;
  border-color: #000;
}
.btn--transparent {
  background-color: transparent;
  color: #000;
  border-color: #000;
}
.calculator_wrapper {
  padding-top: 4em;
  padding-bottom: 4em;
}
.calculator_wrapper .row {
  display: flex;
  flex-direction: row;
  padding: .4em 0;
  align-items: baseline;
}
.calculator_wrapper .row .text:first-of-type {
  width: 15em;
}
.calculator_wrapper .row label {
  width: 15em;
}
.calculator_wrapper .list_row {
  margin-bottom: 2em;
  width: 100%;
}
.calculator_wrapper .list_row .list {
  width: 15em;
}
.calculator_wrapper .list_row .list li {
  width: 100%;
}
.calculator_wrapper .list_row .list li p.text {
  display: inline-block;
}
.calculator_wrapper .list_row > .text:first-of-type {
  width: auto;
  padding-right: 2em;
}
.calculator_wrapper h1 {
  font-size: 2em;
}
.calculator_wrapper .settings_block {
  padding: 2em 0 4em;
}
.calculator_wrapper .control_block {
  display: flex;
  flex-direction: row;
  padding: .8em 1em 4em .8em;
  border: 1px solid #F18700;
  border-radius: 4px;
  align-items: flex-start;
}
.calculator_wrapper .control_block .left {
  display: flex;
  flex-direction: column;
  width: 40%;
  padding-right: 5%;
  align-items: flex-start;
}
.calculator_wrapper .control_block .left .btn {
  min-width: 12em;
  margin-bottom: .6em;
}
.calculator_wrapper .control_block .right {
  width: 65%;
}
.calculator_wrapper .list li {
  list-style-type: none;
}
.calculator_wrapper .table {
  display: flex;
  flex-direction: column;
  margin: 1.5em 0 3em;
}
.calculator_wrapper .table .table_head {
  border-bottom: 1px solid #F18700;
}
.calculator_wrapper .table .table_row {
  display: flex;
  flex-direction: row;
  padding: .7em 0;
}
.calculator_wrapper .table .table_row .cell {
  width: 20%;
}
.calculator_wrapper .table .table_row .cell .input_box {
  padding-right: 1em;
}
.calculator_wrapper .table .table_body .table_row {
  border-bottom: 1px solid #E5E5E5;
}
.calculator_wrapper .table .table_body .cell {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.calculator_wrapper .table .table_body .detail_scheme_color {
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  margin: auto;
}
.calculator_wrapper .table .table_body .table_row:nth-child(1) .detail_scheme_color {
  background-color: #f18700;
}
.calculator_wrapper .table .table_body .table_row:nth-child(2) .detail_scheme_color {
  background-color: #07bd04;
}
.calculator_wrapper .table .table_body .table_row:nth-child(3) .detail_scheme_color {
  background-color: #f10000;
}
.calculator_wrapper .table .table_body .table_row:nth-child(4) .detail_scheme_color {
  background-color: #0300d8;
}
.calculator_wrapper .table .table_body .table_row:nth-child(5) .detail_scheme_color {
  background-color: #ffd400;
}
.calculator_wrapper .table .table_body .table_row:nth-child(6) .detail_scheme_color {
  background-color: #00ffdc;
}
.calculator_wrapper .detail_square {
  position: relative;
  display: inline-block;
  width: 5.6em;
  height: 4.5em;
  margin: auto;
}
.calculator_wrapper .detail_square .square {
  position: absolute;
  top: 50%;
  left: 50%;
  display: inline-block;
  box-sizing: border-box;
  width: 2.8em;
  height: 2.8em;
  transform: translate(-50%, -50%);
  border: 1px solid #F18700;
  background-color: #bfbfbf;
}
.calculator_wrapper .detail_square .square.position_0 {
  border-top: 2px solid #F18700;
}
.calculator_wrapper .detail_square .square.position_1 {
  border-right: 2px solid #F18700;
}
.calculator_wrapper .detail_square .square.position_2 {
  border-bottom: 2px solid #F18700;
}
.calculator_wrapper .detail_square .square.position_3 {
  border-left: 2px solid #F18700;
}
.calculator_wrapper .detail_square .edge_group {
  position: absolute;
  font-size: .7em;
  display: flex;
  justify-content: center;
  align-items: center;
}
.calculator_wrapper .detail_square .edge_group span {
  margin: 0 .15em;
  cursor: pointer;
}
.calculator_wrapper .detail_square .edge_group span.selected {
  font-weight: bold;
}
.calculator_wrapper .detail_square .edge_top {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.calculator_wrapper .detail_square .edge_bottom {
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
.calculator_wrapper .detail_square .edge_right {
  top: 50%;
  right: 0;
  flex-direction: column;
  transform: translateY(-50%);
}
.calculator_wrapper .detail_square .edge_left {
  top: 50%;
  left: 0;
  flex-direction: column;
  transform: translateY(-50%);
}
.calculator_wrapper .cutting_block {
  padding: 4em 0;
}
.calculator_wrapper .cutting_block .plate_box {
  position: relative;
  padding-top: 2.5em;
  padding-left: 2.5em;
  margin-bottom: 5em;
}
.calculator_wrapper .cutting_block .plate_box .text {
  position: absolute;
  top: 0;
  left: 0;
}
.calculator_wrapper .cutting_block .plate_box .plate_item {
  height: 32em;
  width: 100%;
  background-color: #E5E5E5;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  background-image: url('../imgs/ldsp_bgr.jpg');
  background-size: cover;
  background-position: center;
}
.calculator_wrapper .cutting_block .plate_box .plate_item .detail_group {
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
}
.calculator_wrapper .cutting_block .plate_box .plate_item .detail_item {
  display: inline-block;
  border: 1px solid #000;
  position: absolute;
  box-sizing: border-box;
}
.calculator_wrapper .cutting_block .plate_box .plate_item .detail_item .height {
  font-size: .8em;
  position: absolute;
  left: -0.5em;
  bottom: 1em;
  transform: rotate(-90deg);
}
.calculator_wrapper .cutting_block .plate_box .plate_item .detail_item .width {
  font-size: .8em;
  position: absolute;
  top: 0.2em;
  right: 0.3em;
}
.calculator_wrapper .cutting_block .plate_box .plate_item .detail_item.bd-top {
  border-top: 2px solid #000;
}
.calculator_wrapper .cutting_block .plate_box .plate_item .detail_item.bd-right {
  border-right: 2px solid #000;
}
.calculator_wrapper .cutting_block .plate_box .plate_item .detail_item.bd-bottom {
  border-bottom: 2px solid #000;
}
.calculator_wrapper .cutting_block .plate_box .plate_item .detail_item.bd-left {
  border-left: 2px solid #000;
}
.calculator_wrapper .cutting_block .plate_box .size_length,
.calculator_wrapper .cutting_block .plate_box .size_width {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-left: 2em;
}
.calculator_wrapper .cutting_block .plate_box .size_length .text,
.calculator_wrapper .cutting_block .plate_box .size_width .text {
  position: absolute;
  top: .2em;
  left: 50%;
  transform: translateX(-50%);
  font-size: .875em;
  color: #000;
  font-weight: 500;
  line-height: 1.2;
}
@media all and (max-width: 1024px) {
  .calculator_wrapper .cutting_block .plate_box .size_length .text,
  .calculator_wrapper .cutting_block .plate_box .size_width .text {
    font-size: 1em;
  }
}
.calculator_wrapper .cutting_block .plate_box .size_length .arrow,
.calculator_wrapper .cutting_block .plate_box .size_width .arrow {
  position: absolute;
  top: 1.5em;
  right: 0;
  display: inline-block;
  width: calc(100% - 2.5em);
  height: 1px;
  background-color: #bfbfbf;
}
.calculator_wrapper .cutting_block .plate_box .size_length .arrow:before,
.calculator_wrapper .cutting_block .plate_box .size_width .arrow:before,
.calculator_wrapper .cutting_block .plate_box .size_length .arrow:after,
.calculator_wrapper .cutting_block .plate_box .size_width .arrow:after {
  content: '';
  position: absolute;
  top: 45%;
  left: .15em;
  display: inline-block;
  width: .7em;
  height: .7em;
  transform: translateY(-50%) rotate(45deg);
  border-bottom: 1px solid #bfbfbf;
  border-left: 1px solid #bfbfbf;
}
.calculator_wrapper .cutting_block .plate_box .size_length .arrow:after,
.calculator_wrapper .cutting_block .plate_box .size_width .arrow:after {
  right: .15em;
  left: auto;
  transform: translateY(-50%) rotate(225deg);
}
.calculator_wrapper .cutting_block .plate_box .size_width {
  width: auto;
  height: 100%;
  padding-top: 2em;
  padding-left: 0;
}
.calculator_wrapper .cutting_block .plate_box .size_width .text {
  top: 50%;
  left: 0;
  transform: translateY(-50%) rotate(-90deg);
}
.calculator_wrapper .cutting_block .plate_box .size_width .arrow {
  top: 2.5em;
  left: 1.7em;
  width: 1px;
  height: calc(100% - 2.5em);
}
.calculator_wrapper .cutting_block .plate_box .size_width .arrow:before {
  top: 0;
  left: 45%;
  transform: translateX(-50%) rotate(135deg);
}
.calculator_wrapper .cutting_block .plate_box .size_width .arrow:after {
  top: auto;
  bottom: 0;
  left: 45%;
  transform: translateX(-50%) rotate(-45deg);
}
.detail_item {
  position: relative;
}
.detail_item:hover .rotateBtn {
  opacity: 1;
}
.rotateBtn {
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.5em;
  height: 1.5em;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  cursor: pointer;
  transition: .3s;
  opacity: 0;
  background-image: url(data:image/png;base64,);
}
